import { createRouter, createWebHistory, createWebHashHistory } from 'vue-router';
import DemoSlotMachine from '@/demo/SlotMachine.vue';
import SlotMachine from '@/components/SlotMachine.vue';
import InputPage from '@/components/InputPage.vue';

const routes = [
  { path: '/demo', name: 'Demo', component: DemoSlotMachine },
  { path: '/', name: 'SlotMachine', component: SlotMachine },
  { path: '/input', name: "Input", component: InputPage}
];

const router = createRouter({
  history: process.env.NODE_ENV === "production" 
    ? createWebHashHistory()  // ✅ 生产环境（GitHub Pages）用 Hash 模式
    : createWebHistory(),      // ✅ 本地开发用 History 模式
  routes
});

export default router;
